* {
	box-sizing: border-box; }
a {
	color: inherit;
	text-decoration: none; }
.btn-default {
	border-radius: 5px;
	display: inline-block;
	border: 1px solid #fff;
	@include rmin(1200) {
		transition: all .4s;
		position: relative;
		&:hover {
			box-shadow:  0 0 2px #fff; } } }
.btn-big-blue {
	background: $blue-light;
	color: #fff;
	font-size: 21px;
	padding: 7px 12px;
	border-radius: 10px;
	border: none;
	box-shadow:  0 0 2px transparent;
	@include rmin(1200) {
		&:hover {
			box-shadow:  0 0 2px black; } }
	@include r(991) {
		font-size: 17px;
		padding: 0 9px;
		line-height: 32px; }
	@include r(767) {
		font-size: 13px; } }
.inner-wrapper {
	@include rmin(1200) {
		padding:  0 29px; } }
.main-content {
		background: #fff; }
h1 {
	font: 300 100px "Hind", sans-serif;
	color: #fff;
	@include r(992) {
		font-size: 60px; }
	@include r(768) {
		font-size: 50px;
		line-height: 1em; } }
.h1-block {
	text-align: center;
	padding-bottom: 105px;
	padding-top: 30px;
	@include cover;
	@include r(767) {
		padding-top: 25px;
		padding-bottom: 70px; } }
.h1-block__container {
	&>div {
		text-align: center;
		position: absolute;
		padding: 62px 142px 0;
		background: #fff;
		max-width: 1024px;
		top:  173px;
		z-index: 1;
		border-radius: 5px 5px 0 0;
		color: $gray;
		font: $main-font;
		font-size: 18px;
		@include r(767) {
			padding: 50px 80px 0;
			max-width: 512px;
			font-size: 13px;
			line-height: 1.3em; }
		@include r(549) {
			padding: 30px 10px 0!important;
			position: relative;
			top: 0!important;
			margin-top: 20px; }
		&:before {
			content: '';
			position: absolute;
			top: -3px;
			left: 50%;
			transform: translateX(-50%);
			height: 6px;
			width: 71%;
			background: $blue-light; } } }
.socials {
	text-align: center;
	a {
		display: inline-block;
		width: 30px;
		height: 30px;
		border: 2px solid #fff;
		border-radius: 50%;
		position: relative;
		margin-right: 4px;
		@include r(768) {
			width: 22px;
			height: 22px; }
		.icon {
			@include vcenter; }
		@include rmin(1200) {
			transition: all .3s;
			&:hover {
				border: 2px solid #ddd - 20%; } } } }
