.products__h1-block {
	border-bottom: 10px solid #fff;
	@include r(767) {
		border-bottom: 50px solid #fff; }
	@include r(549) {
		padding-bottom: 0; } }
.h1-block__container {
	&>div {
		padding: 36px 74px  0;
		@include r(991) {
			top: 85px; }
		@include r(767) {
			top: 70px;
			padding: 17px 55px  0; } } }
.product-block {
	@include cover;
	padding-top: 125px;
	@include r(991) {
		padding-top: 55px; }
	@include r(549) {
		background-position: 60% 50%; }
	@include r(460) {
		background-position: 80% 50%; }
	.container {
		padding-bottom: 110px;
		&:after {
			content: '';
			position: absolute;
			height: 6px;
			width: 97%;
			bottom: -3px;
			left: 50%;
			transform: translateX(-50%);
			background: $blue; }
		@include r(991) {
			padding-bottom: 64px;
			&:after {
				width: 87%; } } } }
.product-block__inner {
	display: inline-block;
	padding-left: 65px;
	@include r(767) {
		padding-left: 30px; }
	@include r(549) {
		padding-left: 0; } }
.product-block__img {
	width: 225px;
	height: 425px;
	display: inline-block;
	position: absolute;
	top: -50px;
	right: 20px;
	@include cover;
	@include rmin(1500) {
		right: -70px; }
	@include r(991) {
		right: 32px;
		width: 113px;
		height: 213px;
		top: 40px; }
	@include r(767) {
		top: -15px; } }
.product-block__h2 {
	font: 700 86px/0.6em "Hind", sans-serif;
	color: #fff;
	position: relative;
	@include r(767) {
		font-size: 45px; } }
.product-block__short-desc {
	color: $blue;
	font: 700 30px "Hind", sans-serif;
	display: block;
	margin-bottom: 10px;
	position: relative;
	@include r(767) {
		font-size: 15px; } }
.product-block__long-desc {
	font: 400 30px/1.2em "Hind", sans-serif;
	color: #fff;
	width: 380px;
	margin-bottom: 27px;

	position: relative;
	@include r(767) {
		font-size: 15px;
		width: 185px;
		margin-bottom: 12px; } }
.product-block__btn {
	width: 248px;
	height: 80px;
	font: 400 22px/1.1em "Hind", sans-serif;
	color: $blue;
	display: inline-block;
	vertical-align: top;
	text-align: center;
	position: relative;
	border: 2px solid #fff;
	z-index: 2;
	&:first-of-type {
		margin-right: 20px; }
	@include rmin(1200) {
		&:before {
			transition: all .3s;
			@include coverdiv;
			content: '';
			width: 0;
			background: #fff;
			z-index: -1; }
		&:hover:before {
			width: 100%; }
		&:hover {
			box-shadow: none; } }
	@include r(767) {
		width: 120px;
		height: 40px;
		font-size: 11px;
		margin-right: 15px; }
	.icon {
		position: absolute;
		font-size: 1.5em;
		@include vcenter;
		left: 50px;
		@include r(767) {
			left: 27px; } } }
.down-btn {
	padding: 10px 0 0 48px;
	@include r(767) {
		padding: 6px 0 0 30px; }
	span {
		color: #0c445a; } }
.video-btn {
	padding: 27px 0 0 58px;
	@include r(767) {
		padding: 12px 0 0 30px; } }
.product-block:nth-of-type(2n) {
	text-align: right;
	@include r(767) {
		padding-top: 50px;
		.container {
			padding-bottom: 74px; } }
	.product-block__inner {
		text-align: left; }
	.product-block__h2, .product-block__short-desc {
		color: $red; }
	.product-block__long-desc {
		color: $gray;
		background: #fff;
		@include r(767) {
			width: 265px;
			margin-bottom: 25px; } }
	.product-block__btn {
		border-color: #a6a6a6;
		&:hover:before {
			background: #a6a6a6; } }
	.product-block__img {
		right: auto;
		left: 110px;
		@include r(991) {
			left: 45px; } } }
.product-block:last-of-type {
	@include r(767) {
		.container {
			&:after {
				display: none; } } }
	.product-block__short-desc {
		margin-bottom: 0;
		margin-top: 3px; }
	.product-block__long-desc {
		margin-bottom: 50px;
		@include r(767) {
			margin-bottom: 34px; } } }
