.home-page {
	&__h2 {
		font: 600 50px "Hind", sans-serif;
		color: $blue-light;
		margin-top: 59px;
		margin-bottom: 25px;
		@include r(991) {
			margin-top: 17px;
			font-size: 35px;
			margin-bottom: 12px; }
		@include r(549) {
			text-align: center;
			font-size: 25px; } }
	.tabs-wrapper {
		min-height: 418px;
		margin-bottom: 74px;
		@include r(991) {
			min-height: 320px;
			margin-bottom: 0px; } } }
.slider {
	.slide1-content-wrapper {
		float: right;
		@include r(549) {
			float: none;
			text-align: center; }
		h1 {
			max-width: 510px;
			display: inline-block;
			font: 300 70px/1em "Hind", sans-serif;
			text-align: left;
			letter-spacing: -1px;
			margin-right: 15px;
			@include r(991) {
				font-size: 35px;
				max-width: 256px; }
			@include r(549) {
				max-width: 100%;
				display: block;
				margin-right: 0;
				text-align: center; }
			span {
				font-weight: 600; } }
		.btn-default {
			margin-top: 17px;
			font: 400 28px "Hind", sans-serif;
			padding: 0px 16px;
			@include r(991) {
				font-size: 13px;
				padding: 0 7px;
				margin-top: 13px; } } } }
.slide__blockquote {
	max-width: 385px;
	font: 300 25px/1.3em "Hind", sans-serif;
	position: relative;
	&:before {
		z-index: 1;
		content: '';
		position: absolute;
		top: -50px;
		left: 0;
		@include s(com); }
	span {
		font-weight: 600; }
	@include r(991) {
		font-size: 15px;
		&:before {
			@include s(Com-sm);
			top: -20px; } }
	@include r(767) {
		font-size: 13px;
		max-width: 190px; }
	@include r(549) {
		max-width: 100%; } }
.slide__author {
	font: 600 18px "Hind", sans-serif;
	display: inline-block;
	margin-top: 40px;
	@include r(991) {
		font-size: 11px;
		margin-top: 5px; } }
.home-second-slider {
	.container {
		padding-top: 129px;
		padding-left: 17px;
		@include r(991) {
			padding-top: 56px; } } }
